}

.right-metadata-entry{
  padding-left: 20px;
}

p {
  font-size: 14px;
}

.settings{
  width: 100%;
  margin-top: 10px;
}
.opacitySlider{
  width: 80%;
  margin: auto;
  display: block;
}

.settings p{
  margin-block-start: 0px;
  padding-left: 20px;
}

.switch{
  position: relative;
  display: inline-block;
  width: 48px;
  height: 22px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 24px;
  width: 24px;
  left: 0px;
  bottom: -1px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #4ed164;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4ed164;
}

input:checked + .slider:before {
  -webkit-transform: translateX(24px);
  -ms-transform: translateX(24px);
  transform: translateX(24px);
}

.slider.round {
  border-radius: 22px;
}

.slider.round:before {
  border-radius: 50%;
}

.toggleWrapper{
  display: block;
  width: 100%;
  margin-left: 20px;
  margin-top: 12px;
}

.ipolSwitch{
  float: left;
  width: 20%;
}

.ipolLegend{
  float: left;
  width: 80%;
}